import React, { Component } from 'react'
import {observer} from 'mobx-react'
import store from './store'

@observer
export default class index extends Component {
  render() {
    return (
        <div>
          <input value={store.searchText} onChange={store.setSearchText}/>
        </div>
    )
  }
}



