import React, { Component } from 'react'
import './index.css'

import hyperledger from '../../assets/img/HYPERLEDGER@2x.png';
import tech from '../../assets/img/Tech@2x.png';
import mobike from '../../assets/img/MOBIKE@2x.png';
import linuxFoundation from '../../assets/img/linux-foundation@2x.png';
import ucloud from '../../assets/img/UCLOUD@2x.png';
import fujitong from '../../assets/img/fujintong@2x.png';
import qrcode from '../../assets/img/qrcode.png';
import wechat from '../../assets/img/wechat@3x.png';
export default class index extends Component {
  render() {
    return (
      <div className="foot">
        <h1>合作伙伴</h1>
        <div className="partnerLogo">
              <img src={hyperledger} alt=""/>
              <img src={linuxFoundation} alt=""/>
              <img src={mobike} alt=""/>
              <img src={tech} alt=""/>
              <img src={ucloud} alt=""/>
              <img src={fujitong} alt=""/>
        </div>
        <div className="qrcode">
          <img src={qrcode} alt=""/>
          <div>
            <img src={wechat} className="wechat" alt=""/>&nbsp;长按识别二维码,关注微信公众号 <br/>
            企业邮箱:baas-support@dianrong.com
          </div>
        </div>

          <div className="line"></div>

           <p className="copyright">Copyright © 2018 点融区块链 保留所有权利</p>
      </div>
    )
  }
}



